@font-face {
  font-family: "LatoBlack";
  src: local("LatoBlack"), url("./fonts/Lato-Black.ttf") format("truetype");
}

@font-face {
  font-family: "LatoBlackItalic";
  src: local("LatoBlackItalic"),
    url("./fonts/Lato-BlackItalic.ttf") format("truetype");
}

@font-face {
  font-family: "LatoBold";
  src: local("LatoBold"), url("./fonts/Lato-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "LatoBoldItalic";
  src: local("LatoBoldItalic"),
    url("./fonts/Lato-BoldItalic.ttf") format("truetype");
}

@font-face {
  font-family: "LatoItalic";
  src: local("LatoItalic"), url("./fonts/Lato-Italic.ttf") format("truetype");
}

@font-face {
  font-family: "LatoLight";
  src: local("LatoLight"), url("./fonts/Lato-Light.ttf") format("truetype");
}

@font-face {
  font-family: "LatoLightItalic";
  src: local("LatoLightItalic"),
    url("./fonts/Lato-LightItalic.ttf") format("truetype");
}

@font-face {
  font-family: "LatoRegular";
  src: local("LatoRegular"), url("./fonts/Lato-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "LatoThin";
  src: local("LatoThin"), url("./fonts/Lato-Thin.ttf") format("truetype");
}

@font-face {
  font-family: "LatoThinItalic";
  src: local("LatoThinItalic"),
    url("./fonts/Lato-ThinItalic.ttf") format("truetype");
}

:root {
  --main-color: #50e3c2;
  --secondary-color: rgb(34, 34, 34);
  --screen-md-limit: 1024px;
  --screen-sm-limit: 768px;
  --screen-xsm-limit: 480px;
}

html {
  font-size: 20px;
}

body {
  margin: 0;
  font-family: LatoRegular, -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.gutter {
  margin: 0 15.5vw;
}

@media (max-width: 1024px) {
  html {
    font-size: 18px;
  }

  .gutter {
    margin: 0 12vw;
  }
}

@media (max-width: 768px) {
  html {
    font-size: 16px;
  }

  .gutter {
    margin: 0 10vw;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 14px;
  }
}
